* {
  box-sizing: border-box;
}
body {
  background-color: #f9e7df;
  height: 100%;
}
.App {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex: 1;
}
.textStyle {
  color: black;
  font-weight: 700;
  font-size: 16px;
}
@media (min-width: 700px) {
  .App {
    text-align: center;
    /* border: 15px solid red; */
    min-height: 480px;
    padding: 20px;
    width: 100%;
    margin: 50px auto 0px;
  }
}
@media (min-width: 1024px) {
  .App {
    text-align: center;
    /* border: 25px solid red; */
    min-height: 500px;
    padding: 20px;
    width: 100%;
    max-width: 1024px;
    margin: 50px auto 0px;
  }

  .textStyle {
    color: black;
    font-weight: 700;
    font-size: 16px;
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
